.time {
    height: 50%;
    padding-top: 40%;
}
.time span {
    display: block;
    text-align: center;
    font-family: serif;
    color: #99A779;
    font-weight: bolder;
}

#timeWord {
    font-size: 24px;
}

#timeMath {
    font-size: 39px;;
}

.start {
    height: 50%;
    padding-top: 50%;
}

.funBtn {
    border-radius: 5px;
    background-color: #99A779;
    color: #E3EDCD;
    font-size: 20px;
    font-family: serif;
}

.start .funBtn {
    width: 60%;
    margin: 5% 0;
}
