.cardPost, .cardFinalPost {
    width: 95%;
    heIght: 90%;
    border-radius: 3px;
    border: 1px solid #99A779;
    margin: 8px auto;
}


.cardFinalPost span {
    text-align: center;
    display: block;
    font-size: 75px;
    color: #99A779;
}

.cardPostOut {
    width: 100px;
    heIght: 150px;
    border-radius: 6px;
    border: 1px solid #99A779;
    float: left;
    margin: 0 20px ;
    transform: translate(10%, -10%);
}

.cardFinalOut {
    width: 100px;
    heIght: 150px;
    border-radius: 6px;
    border: 1px solid #99A779;
    float: right;
    margin: 0 20px ;
    transform: translate(10%, -10%);
}