.cards {
    width: 100px;
    heIght: 150px;
    border-radius: 6px;
    position: absolute;
    top: 10px;
}

.playArea .heart, .playArea .diamond {
    color: #EE957E;
    background-color: #FDE6E0;
    border: 1px solid #EE957E;
}

.playArea .spade, .playArea .club {
    color: #8497C6;
    background-color: #DCE2F1;
    border: 1px solid #8497C6;
}

.upArea {
    font-size: 20px;
}

.downArea {
    height: 100%;
}

.downArea span {
    font-size: 75px;
    line-height: 148px;
}

#left {
    float: left;
    margin-left: 5px;
}

#right {
    float: right;
    margin-right: 5px;
}