#root, .App, .Home {
    height: 100%;
}
.gameArea {
    float: left;
    width: 85%;
    height: 100%;
    background-color: #E3EDCD;
}

.funcArea {
    float: right;
    width: 15%;
    height: 100%;
    background-color: #D8E6BA;
    text-align: center;
}

[draggable="true"] {
    /*
     To prevent user selecting inside the drag source
    */
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }